* {
    margin: 0px;
    padding: 0px;
}

.App {
    font-family: sans-serif;
    text-align: center;
}

.grid {
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    min-height: 52vh;
    min-width: 350px;
    max-width: 400px;
    gap: 5px;
    padding: 4px;
    background-color: rgb(247, 245, 245);
}

.row {
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    gap: 5px;
}

.tile {
    background-color: royalblue;
    border: 4px solid white;
    flex-grow: 1;
    font-size: 30px;
    width: 80px;
    height: 55px;
    border-radius: 10%;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.visited {
    border: 2px solid black;
}

.vibrate {
    animation: vibrate 0.5s;
    border: 2px solid black;
}

@keyframes vibrate {
    0% {
        transform: rotate(0deg);
    }

    10% {
        transform: rotate(-10deg);
    }

    20% {
        transform: rotate(10deg);
    }

    30% {
        transform: rotate(0deg);
    }

    40% {
        transform: rotate(10deg);
    }

    50% {
        transform: rotate(-10deg);
    }

    60% {
        transform: rotate(0deg);
    }

    70% {
        transform: rotate(-1deg);
    }

    80% {
        transform: rotate(1deg);
    }

    90% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(-1deg);
    }
}

.matched {
    background-color: green;
    border: 2px solid black;
}

.present {
    background-color: grey;
    border: 2px solid black;
}

.notpresent {
    background-color: darkred;
    border: 2px solid black;
}